































































import { Component, Vue, Prop, Watch } from 'vue-property-decorator'
import HelloWorld from '@/components/HelloWorld.vue' // @ is an alias to /src
import Page from '@/components/common-components/page.vue'
import wx from 'weixin-js-sdk'
import { isHasFunc } from '@/until/index'
@Component({
  components: {
    HelloWorld,
    Page
  }
})
export default class Home extends Vue {
  hello : string = 'Hello World!'
  msg = 123
  $createDialog : any
  // 
  model = { inputValue: '', pcaValue: [], dateValue: '' }
  fields =[{ type: 'input', modelKey: 'inputValue', label: 'Input', props: { placeholder: '' }, rules: { required: true } }]
  valid = undefined
  validity = {}
  // 
  houseId = ''
  listData = []
  accountInfo = {}
  showSelect = false
  title = ''
  placeholder = ''
  mounted () {
    // this.pageInit()
  }

  activated () {
    this.pageInit()
  }

  @Prop({ default: 'default value' }) readonly propB!: string

  @Watch('$route')
  onRouterChanged () {
    // this.getWeiXinConfig()
  }

  validateHandler (result : any) {
    this.validity = result.validity
    this.valid = result.valid
    console.log('validity', result.validity, result.valid, result.dirty, result.firstInvalidFieldIndex)
  }
  submitHandler (e : any) {
    console.log('submit')
  }
  goTo (type : any) {
    this.$router.push({ path: '/' + type })
  }
  scanQRCode () {
    let that = this
    wx.scanQRCode({
      needResult: 1, // 01
      scanType: ["qrCode","barCode"], // 
      success: function (res : any) {
        var result = res.resultStr // needResult  1 
        that.$store.dispatch('qrValidate', {qrId: result}).then((res)=>{
          if (res && res.success) {
            that.$createDialog({
              type: 'alert',
              title: '',
              content: '',
              icon: 'cubeic-right'
            }).show()
          } else {
             that.$createDialog({
              type: 'alert',
              title: '',
              content: '',
              icon: 'cubeic-wrong'
            }).show()
          }
        })
      }
    })
  }
  getWeiXinConfig () {
    this.$store.dispatch('getWeixinConfig', { user: '13934691550', pass: 'wangtao' }).then((res)=>{
      console.log('', res)
      if (res.success) {
        let config = res.data
        wx.config({
          debug: false, // ,apialertpclogpc
          appId: 'wxc6c897a704d3fac4', // 
          timestamp: config.timestamp, // 
          nonceStr: config.nonceStr, // 
          signature: config.signature,// 
          jsApiList: ['scanQRCode'] // JS
        });
      }
      wx.ready(function() {
        console.log('ready========')
      })
      wx.error(function(err:any) {
        console.log(err)
        console.log('errrrrrrrrrrrrrr')
      })
    })
  }

  getHouseList () {
    this.$store.dispatch('getHouseList', {}).then((response) => {
      if (response.data.list) {
        this.listData = response.data.list
        this.houseId = this.listData[0].id
        this.$store.commit('SET_HOUSE_LIST', this.listData)
      }
    })
  }

  getBindingInfo () {
    let query = this.$route.query
    if (query.code) {
      const toast = this['$createToast']({
        time: 0,
        txt: '...'
      })
      toast.show()
      this.$store.dispatch('getCodeInfo', { code: query.code }).then((response) => {
        if (response.success) {
          if (response.data.openid) {
            this.openid = response.data.openid
            this.$router.replace({ path: '/login', query: { openid: response.data.openid, jump: '/home' } })
          } else if (response.data.sessionId) {
            this.$store.commit('SET_SESSION_ID', response.data.sessionId)
            this.$store.commit('SET_USER_INFO', response.data.userInfo)
            this.getHouseList()
          }
        }
        toast.hide()
      })
    } else {
      console.log('')
    }
  }

  pageInit () {
    let query = this.$route.query
    if (this.$store.state.sessionId && !this.houseId) {
      this.getHouseList()
    } else if (query.code && !this.$store.state.sessionId) {
      this.getBindingInfo()
    }
  }

  toOtherPage (type: any) {
    if (type) {
      let path = '/' + type
      this.$router.push({ path: path, query: { houseId: this.houseId} })
    }
  }

  get options () {
    this.listData.forEach((obj) =>{
      obj.value = obj.id
      obj.text = obj.account_info[0].name + ' ' + obj.build_info[0].name + ' '+ obj.unitNum + '' + obj.floorNum + '' + obj.roomNum + ''
    })
    return this.listData
  }

  getPower (key: any) {
    let user = this.$store.state.userInfo
    return isHasFunc(key, user)
  }

  getHouseInfo () {
    let info = {}
    this.$store.state.houseList.forEach((obj) =>{
      if (obj.id === this.houseId) {
        info = obj
      }
    })
    return info
  }

  get showAdmin () {
    let houseInfo = this.getHouseInfo()
    if (this.$store.state.userInfo && this.$store.state.userInfo.power && this.$store.state.userInfo.power.length > 0) {
      return this.$store.state.userInfo.adminAccount.indexOf(houseInfo.accountId) > -1
    } else {
      return false
    }
  }
  get showUserHandle () {
    return this.$store.state.userInfo && this.$store.state.userInfo.accountId && this.$store.state.userInfo.accountId.length > 0
  }
}
